import React from "react";
import MypostForm from "./MypostForm/MypostForm";
import s from "./Myposts.module.css";
import Post from "./Post/Post";
import { reduxForm } from "redux-form";

const MypostFormRedux = reduxForm({ form: "mypostForm" })(MypostForm);

//React.memo         ,  

let Myposts = React.memo((props) => {
  let postElements = props.postItems.map((post) => (
    <Post message={post.message} likes={post.likes} key={post.id} />
  ));

  function addPost(values) {
    props.addPost(values.mypostForm);
  }

  return (
    <div>
      <div className={s.postItem}> </div>
      <MypostFormRedux onSubmit={addPost} />
      {postElements}
    </div>
  );
});

export default Myposts;
