import { createSelector } from "reselect";

const getUsersItemsHelper = (state) => {
  return state.usersPage.usersItems;
};

export const getUsersItems = createSelector(
  getUsersItemsHelper,
  (usersItems) => {
    //complex logic here. userItems are cached
    return usersItems;
  }
);

export const getTotalUsers = (state) => {
  return state.usersPage.totalUsers;
};
export const getPageSize = (state) => {
  return state.usersPage.pageSize;
};
export const getPageSelected = (state) => {
  return state.usersPage.pageSelected;
};
export const getIsFetching = (state) => {
  return state.usersPage.isFetching;
};
export const getIsFollowingInProgress = (state) => {
  return state.usersPage.isFollowingInProgress;
};
