export const required = (value) => {
  if (value) {
    return undefined;
  } else {
    return "field is empty";
  }
};

export const maxLenghCreator = (maxLength) => (value) => {
  if (value.length > maxLength) {
    return `maxLength is ${maxLength}`;
  } else {
    return undefined;
  }
};
