import React from "react";
import Loader from "../../common/Loader/Loader";
import s from "./ProfileInfo.module.css";
// import ProfileStatus from "./ProfileStatus/ProfileStatus";
import ProfileStatusFunc from "./ProfileStatus/ProfileStatusFunc";

function ProfileInfo(props) {
  if (!props.profile) {
    return <Loader />;
  } else {
    return (
      <div>
        <div className={s.img}>
          <img
            src="https://gamerwall.pro/uploads/posts/2022-09/1662430532_1-gamerwall-pro-p-krutaya-obezyana-zhivotnie-1.jpg"
            alt=""
          />
        </div>

        <img src={props.profile.photos.large} alt="nice avatar" />
        <ProfileStatusFunc
          {...props}
          status={props.status}
          setStatus={props.setStatus}
          updateStatus={props.updateStatus}
        />
      </div>
    );
  }
}

export default ProfileInfo;
