import React, { useState } from "react";

let ProfileStatusFunc = (props) => {
  const [editMode, setEditMode] = useState(false);

  let activateEditMode = () => {
    setEditMode(true);
  };
  let deactivateEditMode = (e) => {
    setEditMode(false);
    props.setStatus(e.currentTarget.value);
  };
  let onStatusChange = (e) => {
    props.updateStatus(e.currentTarget.value);
  };

  return (
    <>
      {editMode ? (
        <div>
          <input
            type="text"
            value={props.status}
            autoFocus={true}
            onBlur={deactivateEditMode}
            onChange={onStatusChange}
          />
        </div>
      ) : (
        <div>
          <div onClick={activateEditMode}>{props.status || " "}</div>
        </div>
      )}
    </>
  );
};

export default ProfileStatusFunc;
