import { AuthAPI } from "../api/api";
import { stopSubmit } from "redux-form";
const SET_USER_DATA = "SET_USER_DATA";

let initialState = {
  userId: null,
  email: null,
  login: null,
  isAuth: false,
};

export default function authReducer(state = initialState, action) {
  switch (action.type) {
    case SET_USER_DATA: {
      return {
        ...state,
        ...action.data,
        isAuth: action.data.isAuth,
      };
    }

    default:
      return state;
  }
}

export const setUserData = (userId, email, login, isAuth) => ({
  type: SET_USER_DATA,
  data: { userId, email, login, isAuth },
});
export const getUserData = () => async (dispatch) => {
  const response = await AuthAPI.me();
  if (response.data.resultCode === 0) {
    let { id, email, login } = response.data.data;
    dispatch(setUserData(id, email, login, true));
  }
};

export const login = (email, password, remember) => async (dispatch) => {
  let response = await AuthAPI.login(email, password, remember);
  if (response.data.resultCode === 0) {
    dispatch(getUserData());
  } else {
    let errorMessage =
      response.data.messages.length > 0
        ? response.data.messages[0]
        : "Something wrong, I can feel it";
    dispatch(stopSubmit("login", { _error: errorMessage }));
  }
};

export const logout = (email, password, remember) => async (dispatch) => {
  let response = await AuthAPI.logout(email, password, remember);
  if (response.data.resultCode === 0) {
    dispatch(setUserData(null, null, null, false));
  }
};
