let ADD_MESSAGE = "ADD-MESSAGE";

let initialState = {
  dialogItems: [
    { id: 1, name: "" },
    { id: 2, name: "" },
    { id: 3, name: "" },
    { id: 4, name: "" },
    { id: 5, name: "" },
  ],
  messageItems: [
    { id: 1, message: "yo" },
    { id: 2, message: " " },
    { id: 3, message: "  " },
  ],
};

export default function dialogsReducer(state = initialState, action) {
  switch (action.type) {
    case ADD_MESSAGE: {
      return {
        ...state,
        messageItems: [
          ...state.messageItems,
          {
            id: 6,
            message: action.newMessage,
          },
        ],
      };
    }

    default:
      return state;
  }
}

export const addMessgaeActionCreator = (newMessage) => ({
  type: ADD_MESSAGE,
  newMessage,
});
